/* Block */
.breadcrumbs-section {
  padding-top: 70px;
  padding-bottom: 50px;
}
.breadcrumbs-section .breadcrumb {
  padding: 0;
  margin: 0;
  background-color: transparent;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item {
  position: relative;
  font-size: 14px;
  font-weight: 500;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item.active {
  color: #2B478B;
}
.breadcrumbs-section .breadcrumb a {
  color: #767676;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item {
  padding-left: 30px;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  content: "";
  position: absolute;
  top: calc(50% - 4px);
  left: calc(15px - 4px);
  width: 5px;
  height: 8px;
  background-image: url(../../assets/img/svg/angle-right.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

@media (max-width: 991px) {
  .breadcrumbs-section {
    padding-top: 20px;
  }
}
/* Form */
.free-quote-form {
  position: relative;
  padding: 60px 50px 100px;
  border: 1px solid #F2F2F2;
  background-color: #fff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 19px;
}
.free-quote-form::before {
  content: "";
  position: absolute;
  top: -37px;
  right: 20px;
  width: 92px;
  height: 113px;
  background-image: url(../../assets/img/svg/picture/bee-box.svg);
  background-repeat: no-repeat;
  background-size: cover;
}
.free-quote-form::after {
  content: "";
  position: absolute;
  top: 45px;
  right: -520px;
  width: 660px;
  height: 606px;
  background-image: url(../../assets/img/svg/picture/dots-bee-box.svg);
  background-repeat: no-repeat;
  background-size: cover;
}
.free-quote-form .form-title {
  display: inline-block;
  font-size: 24px;
  font-weight: 500;
  margin-bottom: 78px;
}
.free-quote-form .row {
  margin: 0 -10px;
}
.free-quote-form .form-group {
  padding: 0 10px;
  position: relative;
  margin-bottom: 0;
}
.free-quote-form .form-group .form-label {
  position: absolute;
  bottom: 100%;
}
.free-quote-form button[type=submit] {
  height: 100%;
  font-weight: 600;
}

@media (max-width: 991px) {
  .free-quote-form .form-group {
    margin-bottom: 50px;
  }
  .free-quote-form .form-group:last-child {
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .free-quote-form::after, .free-quote-form::before {
    display: none;
  }
  .free-quote-form button[type=submit] {
    width: 100%;
  }
}
@media (max-width: 575px) {
  .free-quote-form {
    padding: 30px 20px 70px;
  }
  .free-quote-form .form-title {
    font-size: 20px;
  }
}
/* Section */
.category-section .category-listing .row {
  margin: 0 -10px;
}
.category-section .category-listing .service-col {
  padding: 0 10px;
  margin-bottom: 50px;
}
.category-section .category-listing .service-card .h2 {
  font-size: 34px;
  font-weight: 600;
  margin-bottom: 0;
}
.category-section .category-listing .service-card img {
  margin-bottom: 40px;
  border-radius: 8px;
}

@media (max-width: 991px) {
  .category-section .category-listing .service-col {
    margin-bottom: 40px;
  }
  .category-section .category-listing .service-col:last-child {
    margin-bottom: 0;
  }
  .category-section .category-listing .service-card img {
    margin-bottom: 25px;
  }
  .category-section .category-listing .service-card .h2 {
    font-size: 28px;
  }
}
@media (max-width: 991px) {
  .category-section .category-listing .service-card img {
    width: 100%;
  }
}
.description-section .image-wrap {
  border-radius: 8px;
  overflow: hidden;
}
.description-section .h1 {
  margin-bottom: 40px;
}
.description-section p {
  color: #767676;
  margin-bottom: 30px;
}
.description-section p:last-child {
  margin-bottom: 0;
}

@media (max-width: 991px) {
  .description-section {
    text-align: center;
  }
  .description-section img {
    width: 100%;
    margin-bottom: 40px;
  }
}
.free-quote-section .container {
  position: relative;
}
.free-quote-section .container::before {
  content: "";
  width: 1453px;
  height: 1089px;
  position: absolute;
  left: 275px;
  top: 76px;
  background-image: url(../../assets/img/svg/picture/6.svg);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}

@media (max-width: 991px) {
  .free-quote-section .container::before {
    display: none;
  }
}
.useful-links-section .row {
  margin: -10px;
}
.useful-links-section .col-link {
  padding: 10px;
}
.useful-links-section .col-link:nth-child(1) .link-wrap {
  background-color: #FFCC00;
}
.useful-links-section .col-link:nth-child(2) .link-wrap {
  background-color: #FED638;
}
.useful-links-section .col-link:nth-child(3) .link-wrap {
  background-color: #FFDC52;
}
.useful-links-section .col-link:nth-child(4) .link-wrap {
  background-color: #FFE98F;
}
.useful-links-section .col-link a:hover {
  text-decoration: none;
}
.useful-links-section .col-link a:hover .learn-more .svg-icon {
  transform: rotate(-180deg);
}
.useful-links-section .col-link .link-wrap {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 40px 20px;
  border-radius: 15px;
}
.useful-links-section .col-link .link-wrap .h2 {
  margin-bottom: 20px;
}
.useful-links-section .col-link .link-wrap p {
  margin-bottom: 20px;
  font-size: 15px;
  font-weight: 500;
  flex: 1;
}
.useful-links-section .col-link .link-wrap .svg-icon {
  margin-right: 5px;
  transition: all 0.2s ease-in-out;
}
.useful-links-section .col-link .link-wrap .learn-more {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  font-weight: 600;
}

.category-section .container {
  position: relative;
}
.category-section .container::before {
  content: "";
  position: absolute;
  top: -130px;
  left: 380px;
  width: 821px;
  height: 602px;
  background-image: url(../../assets/img/svg/picture/8.svg);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: -1;
}

.description-section .container {
  position: relative;
}
.description-section .container::before {
  content: "";
  width: 568px;
  height: 452px;
  position: absolute;
  top: 40px;
  left: -20px;
  background-image: url(../../assets/img/svg/picture/4.svg);
  background-repeat: no-repeat;
  z-index: -1;
  transform: rotate(-28deg) scale(0.95);
}
.description-section .container::after {
  content: "";
  width: 674px;
  height: 911px;
  position: absolute;
  top: 540px;
  left: -520px;
  background-image: url(../../assets/img/svg/picture/9.svg);
  background-repeat: no-repeat;
  z-index: -1;
}

@media (max-width: 1199px) {
  .category-section .container::before {
    top: -110px;
    left: 300px;
    width: 700px;
    height: 495px;
  }

  .description-section .container::after {
    display: none;
  }
  .description-section .container::before {
    top: -40px;
    left: -105px;
  }
}
@media (max-width: 991px) {
  .category-section .container::before {
    display: none;
  }

  .description-section .container::before {
    display: none;
  }
}